@import "_config.scss";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background: $bg-color;
}
// Front Page
.card {
  width: $width;
  margin: 0 auto;
  text-align: left;
  padding: 40px;
  .card-title {
    @extend %title;
  }
  .card-grid {
    margin-top: $margin-top;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: $gap;
  }
}
.game-backdrop {
  @extend %backdrop-styling;
  height: 200px;
}
.game-text {
  margin-top: 10px;
  .game-title {
    @include subInfo(18px, 12px, 0px);
    @extend %title;
  }
  .game-rating {
    @include subInfo(20px, 0px, 20px);
    color: $sub-info-title-col;
    font-weight: 500;
  }
  .released-by {
    @include subInfo(14px, 6px, 0px);
    color: $sub-info-title-col;
    font-weight: 500;
  }
  .title-details {
    font-size: 14px;
    color: $primary-info;
    font-weight: bold;
  }
}

// INDIVIDUAL GAME PAGE

.game-container {
  width: 100%;
  margin: 0 auto;

  .slug-bg {
    height: 500px;
    @extend %backdrop-styling;
  }
  .game-info {
    background: rgba(22, 30, 41, 0.9);
    color: $primary-info;
    padding: 50px;
    @include centering(85%, -50px);
  }
  .esrb-rating {
    margin-left: 40px;
    font-weight: 600;
  }
  .game-description {
    @include centering(90%, $margin-top/2);
    text-align: left;
    line-height: 1.6em;
    color: $primary-info;

    font-weight: 500;
  }
  .tags {
    @include centering(90%, 0px);
    text-align: left;
    opacity: 0.7;
    padding: 20px 0px;
    .tag {
      font-weight: 500;
      @include buttonStyling($highlights, 0px);
    }
    .tag:not(:first-child) {
      margin-left: 20px;
    }
  }
}
.genres {
  margin-left: 40px;
  .genre {
    @extend %flex;
    display: inline;

    .genre-name {
      display: inline;
    }
  }
}
.game-info-inner {
  @extend %flex;
  @include centering(90%, 0px);
  .rating {
    @extend %flex;
    font-size: 36px;
    margin-left: 40px;
  }
}
// Game Stats
.game-stats {
  @include centering(85%, 60px);
  display: grid;
  grid-template-columns: 1fr 1fr;

  .stats-inner {
    @include centering(85%, 0px);
    // display: grid;
    // grid-template-columns: 1fr 1fr;
    // grid-gap: 100px;
    .stats-ratings {
      text-align: left;
    }
  }
}
.status {
  margin-top: 90px;
}
.rating-text,
.status-text {
  line-height: 4em;
  width: 100%;
  @extend %flex;
  .rating-title,
  .status-title {
    color: $sub-info-title-col;
    font-weight: 500;

    font-size: 24px;
  }

  .rating-amount,
  .status-amount {
    color: $sub-info-item-col;
    font-size: 40px;
    margin-left: 60px;
    font-weight: 600;
  }
}
.stats-subtitle {
  color: $highlights;
  font-weight: 700;
  text-align: left;
  font-size: 19px;
}
// Achievement

.achievement-name {
  color: $primary-info;
  opacity: 0.7;
  text-align: left;
  margin-top: 20px;
  font-size: 20px;
}
.achievement-img {
  width: 200px;
  height: 200px;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.achievement-description {
  text-align: left;
  margin-top: 5px;

  font-size: 13px;
}
.achievements-section {
  color: $primary-info;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 50px;
}
.paginate {
  text-align: right;
  .next-page-btn {
    @include buttonStyling($highlights, 30px);
    width: 100px;
    outline: none;
  }
  .next-page-btn:hover,
  .next-page-btn:focus {
    background: $sub-info-item-col;
    transition: 0.5s all ease-in-out;
  }
}
