$bg-color: #01151a;
$primary-info: #41597a;
$highlights: #33588b;

$sub-info-title-col: #3e516b;
$sub-info-item-col: #2c3a4d;

// Dimensions
$width: 80%;
$margin-top: 56px;
$gap: 48px;

// Mixins,modules etc..

%backdrop-styling {
  background-size: cover;
  background-repeat: no-repeat;
}
%title {
  color: $primary-info;
  font-weight: 600;
}
%flex {
  display: flex;
  align-items: center;
}
@mixin buttonStyling($bg, $m-top) {
  border-style: none;
  border-radius: 100px;
  padding: 11px 17px;
  background: $bg;
  margin-top: $m-top;
}
@mixin centering($w, $m-top) {
  margin: 0 auto;
  margin-top: $m-top;
  width: $w;
}
@mixin subInfo($size, $margin, $m-left) {
  display: inline-block;
  margin-top: $margin;
  margin-left: $m-left;
  font-size: $size;
}
